body {
    font-family: Arial, sans-serif;
    background: #f3f4f6;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
  }
  
  .App {
    background: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
    max-width: 400px;
    width: 100%;
  }
  
  .App h1 {
    color: #333;
    margin-bottom: 20px;
  }
  
  .App input {
    width: calc(100% - 20px);
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }
  
  .App button {
    width: calc(100% - 20px);
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    margin-bottom: 10px;
  }
  
  .App button.google-btn {
    background-color: #db4437;
  }
  
  .App button:hover {
    background-color: #0056b3;
  }
  
  .App p {
    margin-top: 10px;
    font-size: 14px;
  }
  
  .App p span {
    color: #007bff;
    cursor: pointer;
    text-decoration: underline;
  }
  